<template>
  <div>
    <Modal
      v-model="modal1"
      :title="title + '数据'"
      class-name="vertical-center-modal"
      width="1000px"
      @on-ok="ok"
      @on-cancel="cancel"
    >
      <div>
        <Row :gutter="16">
          <Col span="12">
            <div id="demo" style="width: 400px;height:400px;"></div>
          </Col>
          <Col span="4">
            <div id="cicle" style="width: 400px;height:400px;"></div>
          </Col>
        </Row>
      </div>
    </Modal>
  </div>
</template>

<script>
import opts from "../config";
export default {
  props: {
    title: [String]
  },

  data: () => ({
    modal1: false
  }),

  created() {
    setTimeout(() => {
      this.modal1 = true;
    }, 0);
  },
  mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById("demo"));

    // 指定图表的配置项和数据
    var option = {
      ...opts,

      title: {
        text: `${this.title} 销量top5`,
        textStyle: {
            color: '#fff'
        }
      },
      tooltip: {},
      xAxis: {
        data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋"],
        textStyle: {
            color: '#fff'
        }
      },
      yAxis: {
        color: ["#3398DB", "#ccc", "#eee", "#f7f898", "red"]
      },
      series: [
        {
          name: "销量",
          type: "bar",
          data: [5, 20, 36, 10, 10]
        }
      ]
    };

    // 使用刚指定的配置项和数据显示图表。
    myChart.setOption(option);

    // 基于准备好的dom，初始化echarts实例
    var myChartCircle = echarts.init(document.getElementById("cicle"));

    // 指定图表的配置项和数据
    var optionCircle = {
      ...opts,
      title: {
        text: `${this.title}地区年龄占比`,
        textStyle: {
            color: '#fff'
        }
      },
      tooltip: {
        trigger: "item",
        formatter: "{a} <br/>{b}: {c} ({d}%)"
      },
      legend: {
        orient: "vertical",
        x: "left",
        data: ["<12", "12-15", "15-20", "20-25", "25-30", ">30"],
        textStyle: {
            color: '#fff'
        }
      },
      series: [
        {
          name: "访问来源",
          type: "pie",
          radius: ["50%", "70%"],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: "center"
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: "30",
                fontWeight: "bold"
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: [
            { value: 10, name: "<12" },
            { value: 20, name: "12-15" },
            { value: 25, name: "15-20" },
            { value: 10, name: "20-25" },
            { value: 10, name: "25-30" },
            { value: 20, name: ">30" }
          ]
        }
      ]
    };

    // 使用刚指定的配置项和数据显示图表。
    myChartCircle.setOption(optionCircle);
  },
  methods: {
    ok() {
      this.$emit("close");
    },
    cancel() {
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
p {
  color: yellow;
}

.vertical-center-modal {
  color: #283857;
}
.ivu-modal-content {
  background-color: rgba($color: #000000, $alpha: 0.6);
}
.ivu-modal {
    .ivu-modal-content {
        background-color: rgba($color: #000000, $alpha: 0.6);
    }
}
