import Vue from 'vue'
import Vuex from 'vuex'
import { rd } from './utils/methods.js'
import { geoCoordMapD } from './views/data.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    list: [],
  },
  mutations: {
    // login(state, user) {
    //   state.user = user
    // },
    updateMapData(state, list) {
      // state.list = list
      const end = list.end
      const idx = state.list.findIndex(e => e.end === end)
      if (idx !== -1) {
        state.list[idx].type = list.type
        state.list[idx].start = list.start
        state.list[idx].end = list.end
        state.list[idx].value = list.value
        state.list[idx].period = list.period
        state.list[idx].name = list.name
      } else {
        state.list.push(list)
      }
      console.log(state.list)
    }
  },
  actions: {

  }
})
