import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "iview/dist/styles/iview.css";
import iView from "iview";
import { ip } from './views/data'

Vue.use(iView);

Vue.config.productionTip = false;

var i = io(`http://${ip}:3100`);

const eb = new Vue();

i.on("data", e => {
  eb.$emit("data", e);
});

Vue.prototype.$eb = eb;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
